.Player {
  width: 40vw;
  height: 30px;
  line-height: 30px;
  display: -ms-flexbox;
  display: flex;
  margin: 12px 0;
}

.Player_name {
  border: 1px solid #2b2b2b;
}

.Player_score {
  border-top: 1px solid #2b2b2b;
  border-bottom: 1px solid #2b2b2b;
  border-right: 1px solid #2b2b2b;
  font-weight: bold;
}

.plus_button, .minus_button {
  margin-left: 3px;
  font-size: 22px;
}

.Player_name {
  -ms-flex: 7 1;
      flex: 7 1;
  text-transform: uppercase;
  padding-left: 5px;
}

.Player_score {
  -ms-flex: 2 1;
      flex: 2 1;
  text-align: center;
}

.Player_button {
  -ms-flex: 1 1;
      flex: 1 1;
  text-align: center;
  cursor: pointer;
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
}

.plus_button {
  background-color: #3a8e3a;
}

.plus_button:active {
  background-color: #118911;
}

.minus_button {
  background-color: #cd4949;
}

.minus_button:active {
  background-color: #ba2121;
}

.Remove_button {
  padding-left: 10px;
  cursor: pointer;
}

body {
  counter-reset: section;
}

li::before {
  counter-increment: section;
  content: counter(section);
  width: 30px;
  text-align: center;
  border: 1px solid #2b2b2b;
}

@media (max-width: 768px) {
  .Player {
    width: 80vw;
  }
}



